import tuiIcon from "@/components/icon/icon"
import tuiBadge from "@/components/badge/badge"
import tuiLoadmore from "@/components/loadmore/loadmore"
export default {
	components:{
		tuiIcon,
		tuiBadge,
		tuiLoadmore
	},
	data() {
		return {
			loadding: false,
			show: false,
			bottom: 0
		}
	},
	onLoad: function(options) {
	},
	methods: {

	},
	onPageScroll(e) {
		if (e.scrollTop == 0 && !this.loadding) {
			this.loadding = true
			setTimeout(() => {
				this.show = true;
				this.loadding = false
			}, 1000)
		}
	}
}
